html {
  background-color: #fff;
  position: relative
}

body,
html {
  min-height: 100%
}

body {
  margin: 0 0 119px;
  padding: 0;
  color: #2c2e2f;
  font-family: pp-sans-small-regular, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal;
  font-size: 93.75%;
  -webkit-font-smoothing: antialiased;
  -webkit-backface-visibility: hidden;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%
}

li,
ol,
ul {
  margin: 0;
  padding: 0
}

p {
  font-family: pp-sans-small-light, Helvetica Neue, Arial, sans-serif
}

h1,
p {
  font-weight: 400;
  font-variant: normal;
  color: #2c2e2f
}

h1 {
  margin-bottom: 10px;
  font-family: pp-sans-big-light, Helvetica Neue, Arial, sans-serif;
  font-size: 2em;
  line-height: 33px
}

h2 {
  padding: 0;
  font-family: pp-sans-small-light, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal;
  font-size: 1.6em;
  color: #2c2e2f
}

h2,
h4 {
  margin: 0
}

img {
  border: 0
}

a,
a:link,
a:visited {
  color: #0070ba;
  font-family: pp-sans-small-regular, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal;
  text-decoration: none;
  -webkit-transition: color .2s ease-out;
  -moz-transition: color .2s ease-out;
  -o-transition: color .2s ease-out;
  transition: color .2s ease-out
}

a:active,
a:focus,
a:hover {
  color: #005ea6;
  outline: none;
  text-decoration: underline
}

.dusk,
.dusk:link,
.dusk:visited {
  color: #0079ad
}

header {
  padding: 15px
}

.no-js body {
  display: block!important
}

.nonjsAlert {
  margin: 0;
  padding: .5em;
  border-top: 1px solid #900;
  border-bottom: 1px solid #900;
  background-color: #ff9;
  color: #900;
  text-align: center
}

.imageLink:focus {
  -webkit-box-shadow: 0 0 1px 1px #666;
  -moz-box-shadow: 0 0 1px 1px #666;
  box-shadow: 0 0 1px 1px #666;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px
}

.accessAid {
  position: absolute!important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0!important;
  border: 0!important;
  height: 1px!important;
  width: 1px!important;
  overflow: hidden
}

.clearfix {
  zoom: 1
}

.clearfix:after,
.clearfix:before {
  display: table;
  content: ""
}

.clearfix:after {
  clear: both
}

.hide {
  display: none
}

.underlay {
  content: "";
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  background: #999;
  opacity: .6;
  z-index: 5
}

input::-ms-clear,
input::-ms-reveal {
  display: none;
  width: 0;
  height: 0
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box
}

.paypal-logo {
  margin: 0 auto;
  background: transparent url(https://www.paypalobjects.com/images/shared/monogram@2x.png) top no-repeat;
  -moz-background-size: 30px 37px;
  background-size: 30px 37px;
  width: 30px;
  height: 37px;
  display: block;
  text-indent: -999em
}

.contentContainer {
  position: relative;
  width: 400px;
  margin: 135px auto 0;
  padding: 0;
  background-color: #fff
}

.contentContainer .modal-overlay {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 16;
  -moz-opacity: .9;
  -khtml-opacity: .9;
  -webkit-opacity: .9;
  opacity: .9;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=90);
  filter: alpha(opacity=90);
  background-color: #fff
}

.contentContainer header {
  padding: 0 0 20px
}

footer {
  left: 0;
  height: 119px
}

footer,
footer ul {
  position: absolute;
  bottom: 0;
  width: 100%
}

footer ul {
  list-style-type: none;
  height: 42px;
  text-align: center;
  background-color: #f7f9fa
}

footer ul li {
  display: inline-block;
  margin: 0;
  padding: 15px 0 15px .4em;
  font-size: .73333333em
}

footer ul li:first-child {
  background: none
}

footer ul li:last-child {
  border-right: 0
}

footer ul li a,
footer ul li a:focus,
footer ul li a:hover,
footer ul li a:link,
footer ul li a:visited {
  color: #666;
  margin-right: .5em;
  white-space: nowrap;
  font-family: pp-sans-small-light, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal
}

footer .paypal-logo {
  background: transparent url(https://www.paypalobjects.com/images/shared/monogram-small@2x.png) top no-repeat;
  -moz-background-size: 20px 24px;
  background-size: 20px 24px;
  width: 20px;
  height: 24px
}

.response500 {
  text-align: center
}

.response500 .headerText {
  line-height: normal
}

.left {
  text-align: left
}

.bold {
  font-weight: 700;
  margin-bottom: 0
}

.largetext {
  font-size: 30px;
  margin-top: 0
}

.countdown {
  font-size: 38px;
  margin-top: 0
}

.spacer {
  border: 1px solid #e5e8ea
}

@media (max-width:767px) {
  header {
    padding-bottom: 15px
  }
  .contentContainer {
    margin-top: 30px
  }
}

@media (max-width:414px) {
  .contentContainer {
    margin-top: 30px;
    padding: 0 10% 30px;
    width: 100%;
    background-color: transparent
  }
}

.tryDifferentWaySection {
  margin-top: 20px;
  text-align: center
}

.action {
  margin-top: 30px
}

.infoSection {
  margin: 20px 0
}

.textCenter {
  text-align: center
}

.top40 {
  margin-top: 40px
}

.top20 {
  margin-top: 20px
}

.top15 {
  margin-top: 15px
}

.errorIcon {
  background: transparent url(https://www.paypalobjects.com/images/shared/lg-attention-critical.png) top no-repeat;
  -moz-background-size: 108px 105px;
  background-size: 108px 105px
}

.errorIcon,
.warningIcon {
  width: 108px;
  height: 105px;
  display: block;
  text-indent: -999em;
  margin: 0 auto
}

.warningIcon {
  background: transparent url(https://www.paypalobjects.com/images/shared/lg-attention-warning.png) top no-repeat;
  -moz-background-size: 108px 105px;
  background-size: 108px 105px
}

.closeIconSection {
  cursor: pointer;
  float: right;
  padding: 15px
}

.lightbox {
  margin: 0 0 60px
}

.lightbox header {
  display: none
}

.successAnimation {
  background: transparent url(https://www.paypalobjects.com/images/shared/successAnimation-@2x.gif) no-repeat;
  height: 105px;
  margin: 0 auto;
  width: 105px;
  -moz-background-size: 100% 100%;
  background-size: 100% 100%
}

.modal-animate {
  z-index: 17;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  min-height: 100%;
  overflow: hidden
}

.rotate {
  background: transparent url(https://www.paypalobjects.com/images/shared/animation-oval.png) top no-repeat;
  -moz-background-size: 100px 100px;
  background-size: 100px 100px;
  width: 100px;
  height: 100px;
  border: 0;
  left: 50%;
  top: 50%;
  z-index: 6
}

.rotate,
.rotate:before {
  content: "";
  position: absolute
}

.rotate:before {
  left: -2px;
  top: -2px;
  width: 104px;
  height: 104px;
  border-top: 7px solid #fff;
  border-right: 7px solid #fff;
  border-bottom: 7px solid #fff;
  border-left: 7px solid transparent;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-animation: rotation 1s linear infinite;
  -moz-animation: rotation 1s linear infinite;
  -o-animation: rotation 1s linear infinite;
  animation: rotation 1s linear infinite
}

.processing,
.rotate {
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0
}

.processing {
  top: 130px;
  height: 17px;
  z-index: 17;
  font-size: 15px;
  font-family: pp-sans-small-regular, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal
}

.loaderOverlayAdditionalElements,
.processing {
  width: 100%;
  text-align: center;
  position: absolute
}

.loaderOverlayAdditionalElements {
  top: 68%
}

.loaderOverlayAdditionalElements .spacer {
  margin: 1em
}

.loadingContainer {
  width: 100%;
  height: 60px;
  padding: 15px;
  text-align: left;
  background: #f7f9fa
}

.loadingContainer .text {
  color: #393f44;
  font-size: 15px;
  font-family: pp-sans-small-regular, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal;
  margin-left: 15px
}

.loadingContainer .loading {
  background: transparent url(https://www.paypalobjects.com/images/shared/animation-oval.png) top no-repeat;
  -moz-background-size: 30px 30px;
  background-size: 30px 30px;
  width: 30px;
  height: 30px;
  border: 0;
  display: inline-block;
  vertical-align: middle;
  position: relative
}

.loadingContainer .loading:before {
  left: -2px;
  top: -2px;
  position: absolute;
  content: "";
  width: 34px;
  height: 34px;
  border-top: 7px solid #f7f9fa;
  border-right: 7px solid #f7f9fa;
  border-bottom: 7px solid #f7f9fa;
  border-left: 7px solid transparent;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-animation: rotation 1s linear infinite;
  -moz-animation: rotation 1s linear infinite;
  -o-animation: rotation 1s linear infinite;
  animation: rotation 1s linear infinite
}

@-webkit-keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2)
  }
}

@-moz-keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2)
  }
}

@-o-keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2)
  }
}

@keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2)
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

