.unsupportedBrowserPage {
  text-align: center
}

.unsupportedBrowserPage p {
  margin-top: 20px
}

.unsupportedBrowserPage h1 {
  font-size: 1.9em
}

.block {
  display: block
}

.browserText {
  font-family: pp-sans-small-medium;
  font-weight: 500
}

.safari {
  background: url(https://www.paypalobjects.com/images/shared/sprite-browsers.png) no-repeat -1140px -185px;
  width: 68px
}

.firefox,
.safari {
  height: 74px;
  margin: 0 auto
}

.firefox {
  background: url(https://www.paypalobjects.com/images/shared/sprite-browsers.png) no-repeat -1040px -185px;
  width: 66px
}

.edge {
  background: url(https://www.paypalobjects.com/images/shared/sprite-browsers.png) no-repeat -1040px -100px;
  width: 68px
}

.chrome,
.edge {
  height: 74px;
  margin: 0 auto
}

.chrome {
  background: url(https://www.paypalobjects.com/images/shared/sprite-browsers.png) no-repeat -1140px -100px;
  width: 67px
}

.browserIcon {
  width: 25%;
  float: left
}

.twofactorPage {
  text-align: center
}

.twofactorPage button[type=submit] {
  font-weight: 400;
  font-weight: 700
}

.twofactorPage .tryAnotherWay,
.twofactorPage button[type=submit] {
  font-family: pp-sans-small-regular, Helvetica Neue, Arial, sans-serif;
  font-variant: normal
}

.twofactorPage .tryAnotherWay {
  font-weight: 400
}

.twofactorPage .vx_hr-alt {
  margin: 5% 0
}

.twofactorPage .vx_alert {
  margin-top: 24px
}

.twofactorPage .vx_alert-text {
  text-align: left;
  padding-left: 3%
}

.twofactorPage .vx_alert-warning {
  color: #d64003;
  background-color: #fffbf5
}

.twofactorPage .vx_alert-critical {
  color: #d20000;
  background-color: #fff7f7
}

.twofactorPage .input.textInput {
  margin-top: 12px
}

.twofactorPage .heading {
  margin-bottom: 10px;
  font-family: pp-sans-big-light, Helvetica Neue, Arial, sans-serif;
  font-weight: 400;
  font-variant: normal;
  font-size: 2em;
  color: #2c2e2f;
  line-height: 33px
}

.twofactorPage .vx_radio {
  width: 100%;
  position: relative
}

.twofactorPage .vx_radio input {
  opacity: 0;
  position: absolute;
  cursor: pointer
}

.twofactorPage .vx_radio input:focus+label:before {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px
}

.twofactorPage .vx_radio input:checked+label:before {
  content: "\2611";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff
}

.twofactorPage .vx_radio label {
  cursor: pointer;
  display: inline-block;
  vertical-align: top;
  line-height: 1.5em;
  width: 100%;
  text-align: left;
  padding-left: 5%
}

.twofactorPage .vx_radio label:before {
  font-family: paypal-vx-icons;
  display: inline-block;
  vertical-align: top;
  height: 1em;
  width: 1em;
  margin: 0 .5em 1.25em 0;
  padding: 0;
  border: 1px solid #b7bcbf;
  content: "";
  font-size: 23px;
  font-weight: bolder;
  line-height: 0;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  overflow: hidden
}

.twofactorPage .vx_radio label span {
  vertical-align: middle
}

.twofactorPage .vx_radio input:checked+label:before {
  content: "";
  border-color: #005ea6;
  background-color: #005ea6;
  -webkit-box-shadow: inset 0 0 0 5px #fff;
  -moz-box-shadow: inset 0 0 0 5px #fff;
  box-shadow: inset 0 0 0 5px #fff
}

.twofactorPage .vx_radio label:before {
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%
}

.twofactorPage .skiptwofactorlabel {
  text-align: left;
  font-family: pp-sans-small-light, Helvetica Neue, Arial, sans-serif
}

.twofactorPage #skipTwofactorCheckbox,
.twofactorPage .checkboxContainer,
.twofactorPage .skiptwofactorlabel {
  display: inline-block;
  vertical-align: top
}

.twofactorPage .checkboxContainer {
  text-align: left;
  margin-bottom: 35px
}

.twofactorPage .vx_checkbox {
  width: 100%;
  position: relative
}

.twofactorPage .vx_checkbox input {
  opacity: 0;
  position: absolute;
  cursor: pointer
}

.twofactorPage .vx_checkbox input:focus+label:before {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px
}

.twofactorPage .vx_checkbox input:checked+label:before {
  content: "\2611";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-color: #005ea6;
  color: #fff;
  background-color: #005ea6
}

.twofactorPage .vx_checkbox label {
  cursor: pointer;
  display: inline-block;
  vertical-align: top;
  line-height: 1.6em
}

.twofactorPage .vx_checkbox label:before {
  font-family: paypal-vx-icons;
  display: inline-block;
  vertical-align: top;
  height: .2em;
  width: 1em;
  margin: 0 12px 0 0;
  padding: .4em 0;
  border: 1px solid #b7bcbf;
  content: "";
  font-size: 22px;
  font-weight: bolder;
  line-height: 0;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  overflow: hidden
}

.no-js .captcha-container .captcha-inputs .refresh.small.light {
  display: none
}

.captcha-overlay {
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  overflow: auto;
  background-color: rgba(0, 0, 0, .6)
}

.captcha-container {
  width: 100%;
  margin: 0
}

.captcha-container .container {
  width: 95%;
  background-image: none;
  background-color: transparent
}

.captcha-container .corral {
  margin: 0 auto;
  width: 460px
}

.captcha-container .contentContainer {
  position: relative;
  margin: 50px auto 0;
  padding: 5% 10%;
  background-color: #fff;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px
}

.captcha-container .contentContainer .modal-overlay {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #fafafa;
  z-index: 16;
  opacity: 1
}

.captcha-container .contentContainer .modal-animate {
  z-index: 17;
  position: absolute;
  width: 100px;
  margin: 0 auto;
  top: 120px;
  left: 38%;
  min-height: 100px;
  overflow: hidden
}

.captcha-container .contentContainer .rotate {
  content: "";
  height: 30px;
  width: 30px;
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 6;
  margin: -15px auto auto -15px;
  -webkit-animation: rotation .7s infinite linear;
  -moz-animation: rotation .7s infinite linear;
  -o-animation: rotation .7s infinite linear;
  animation: rotation .7s infinite linear;
  border-left: 8px solid rgba(0, 0, 0, .2);
  border-right: 8px solid rgba(0, 0, 0, .2);
  border-bottom: 8px solid rgba(0, 0, 0, .2);
  border-top: 8px solid #2180c0;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%
}

.captcha-container .contentContainer .paypal-logo {
  margin: 0 auto;
  background: transparent url(https://www.paypalobjects.com/images/shared/monogram@2x.png) top no-repeat;
  -moz-background-size: 30px 37px;
  background-size: 30px 37px;
  width: 30px;
  height: 37px;
  display: block;
  text-indent: -999em
}

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg)
  }
}

@-moz-keyframes rotation {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  to {
    -moz-transform: rotate(359deg);
    transform: rotate(359deg)
  }
}

@-o-keyframes rotation {
  0% {
    -o-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  to {
    -o-transform: rotate(359deg);
    transform: rotate(359deg)
  }
}

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(359deg);
    -moz-transform: rotate(359deg);
    -o-transform: rotate(359deg);
    transform: rotate(359deg)
  }
}

.captcha-container .headerText {
  text-align: center;
  margin: 0 10px 0 0;
  border-bottom: 1px solid #c1c1c1;
  padding-bottom: 5px;
  font-size: 30px;
  font-weight: 400
}

.captcha-container .headerText .notYou {
  color: #6c7378;
  font-size: 15px;
  padding-bottom: 10px
}

.captcha-container .captcha-paypal-logo {
  margin: 0 auto 30px;
  background: transparent url(https://www.paypalobjects.com/images/shared/paypal-logo-129x32.svg) top no-repeat;
  -moz-background-size: 129px 32px;
  background-size: 129px 32px;
  width: 129px;
  height: 32px;
  display: block;
  text-indent: -999em
}

.lower-than-ie9 .captcha-container .captcha-paypal-logo {
  background: transparent url(https://www.paypalobjects.com/images/shared/paypal-logo-129x32.png) top no-repeat
}

.captcha-container .captcha-image {
  height: 85px;
  width: 100%;
  text-align: center;
  border: 1px solid #e6e6e6;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  margin: 0 0 5px
}

.captcha-container .captcha-image img {
  width: 100%;
  height: 81px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px
}

.captcha-container .captcha-inputs {
  display: table;
  width: 100%;
  height: 50px
}

.captcha-container .captcha-inputs .textInput {
  display: table-cell;
  min-width: 100px
}

.captcha-container .captcha-inputs .audio,
.captcha-container .captcha-inputs .refresh {
  display: table-cell;
  vertical-align: middle;
  width: 36px
}

.lower-than-ie9 .captcha-container .captcha-inputs .audio,
.lower-than-ie9 .captcha-container .captcha-inputs .refresh,
.lower-than-ie10 .captcha-container .captcha-inputs .audio,
.lower-than-ie10 .captcha-container .captcha-inputs .refresh {
  vertical-align: bottom
}

.captcha-container .captcha-inputs .refresh {
  padding: 0 8px
}

.captcha-container .captcha-inputs .captchaPlay,
.captcha-container .captcha-inputs .captchaRefresh {
  display: block;
  width: 36px;
  height: 36px
}

